.cms-menu.collapsed .cms-menu-list {
  float: left;
}

.cms-menu.collapsed .cms-fluent-selector .text {
  display: none;
}

.cms-menu.collapsed .cms-fluent-selector-locales {
  width: 100%;
}

.cms-menu.collapsed .cms-fluent-selector-locales .full-title {
  display: none;
}

.cms-menu.collapsed .cms-fluent-selector-locales .short-title {
  display: block;
}

.cms-tree.jstree .status-fluentinvisible a {
  color: #566b8d;
}

.cms-fluent-selector {
  display: block;
  height: auto;
  line-height: 1.5;
  overflow: visible;
  padding: 5px;
  position: relative;
}

.cms-fluent-selector:before {
  color: white;
  position: absolute;
  top: 50%;
  line-height: 0;
  right: 24px;
  width: 0;
  pointer-events: none;
}

.cms-panel-layout.collapsed .cms-fluent-selector:before {
  right: 36px;
}

.cms-fluent-selector .cms-fluent-selector-locales {
  background-color: #368b39;
  background-image: url("../images/menu-icon-24-white.png");
  background-position: 6px center;
  background-repeat: none;
  background-size: 12px;
  border-radius: 5px;
  border: 0 none;
  color: white;
  padding: 5px 21px 5px 24px;
  width: 100%;
}

.cms-panel-layout.collapsed .cms-fluent-selector .cms-fluent-selector-locales {
  color: transparent;
  padding: 2px;
}

.cms-panel-layout.collapsed .cms-fluent-selector .cms-fluent-selector-locales > option {
  color: white;
}

.fluent-badge {
  color: #fff;
  margin-left: 5px;
}

.fluent-badge--default {
  background-color: #008a00;
}

.fluent-badge--invisible {
  background-color: #566b8d;
}

.fluent-badge--localised {
  background-color: #660ff2;
}

.fluent__localised-field .font-icon-translatable {
  color: #660ff2;
  top: 2px;
  position: relative;
  margin-right: 3px;
}

.cms .ss-ui-action-tabset.ss-tabset.fluent-actions-menu {
  margin-left: 5px;
}

.cms .ss-ui-action-tabset.ss-tabset.fluent-actions-menu .popover-actions-simulate .ui-tabs-anchor {
  font-size: 1.0em;
  color: #303b4d;
}

.cms .ss-ui-action-tabset.ss-tabset.fluent-actions-menu .popover-actions-simulate .ui-tabs-anchor:before {
  margin-right: 6px;
  font-size: 1.5rem;
}

.cms .ss-ui-action-tabset.ss-tabset.fluent-actions-menu .popover-actions-simulate .ui-tabs-anchor:after,
.cms .ss-ui-action-tabset.ss-tabset.fluent-actions-menu .popover-actions-simulate .ui-tabs-anchor:hover:after,
.cms .ss-ui-action-tabset.ss-tabset.fluent-actions-menu .popover-actions-simulate .ui-tabs-anchor:active:after {
  display: none !important;
}

.cms .ss-ui-action-tabset.ss-tabset.fluent-actions-menu .tab-pane {
  padding-top: 0;
}

.cms .ss-ui-action-tabset.ss-tabset.fluent-actions-menu .tab-pane .action-description {
  font-size: 0.9em;
  line-height: 1em;
  display: block !important;
  margin: 0;
  padding: 0 11px 8px 11px;
  border-bottom: 1px solid #ccc;
  color: #999;
}

.cms .ss-ui-action-tabset.ss-tabset.fluent-actions-menu .tab-pane .action-description:last-child {
  border-bottom: 0 none;
}




